
body {
    margin: 0px 0px 0px 0px;
}
span, p {
    padding: 2px 2px 2px 2px;
}
h1 {
    text-align: center;
}
.flag {
    margin-left: 6px;
    margin-right: 6px
}
.speechAsText {
    font-style: italic;
    font-weight: bold;
}
.listStart {
    border-bottom-width: 1px;
    border-left-width: 0px;
    border-top-width: 0px;
    border-right-width: 0px;
    border-color: black;
    border-style: solid;
    padding-bottom: 2px;
    margin-bottom: 16px;
}
.backgroundDiv {
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.background {
    height: 100%;
    z-index: -1;
    opacity: 0.3;
}
.expand {
    width: 100%;
    height: 100%;
}
.absoluteCenter {
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    position: absolute;
}
.column, .row {
    display: flex;
    align-items: center;
}
.column { flex-direction: column; }
.row { flex-direction: row; }
